import React from 'react';
import { Link } from 'react-router-dom';
import classNames from 'classnames/bind'
import states from '../config/states'
import StandardLayout from './StandardLayout'
import StandardForm from './StandardForm'
import styles from './Contact.module.scss';

const cx = classNames.bind(styles)

const Contact = () =>
  <StandardLayout>
    <div className={cx('contact')}>
      <div className={cx('contact-text')}>
        <h1>Contact Us</h1>
        <hr/>
        <p>
          If you are concerned about a loose animal, unleashed dog, abuse or neglect of an animal,
          contact Hillsborough County Pet Resource Center at (813) 744-5660.
        </p>
        <p>
          We are an all volunteer group and therefore we can't always respond as quickly as we
          would like. If you need to get an answer now or have an immediate need regarding a dog
          or cat, please contact the Humane Society of Tampa at (813) 876-7138 or Hillsborough
          County Pet Resource Center at (813) 744-5660.
        </p>
        <p>
          We would like to take in all animals but we are LIMITED due to space in foster homes.
          Our responses will be based on the availability of space and funds available.
        </p>
        <p>
          If you are inquiring about volunteering for St Francis and are at least 18 years old,
          fill out a <Link to='/volunteer'>Volunteer Form</Link>.
        </p>
      </div>

      <h2>Contact Form</h2>
      <StandardForm action='http://stfrancisrescue.org/contact_forms' className={cx('form')}>
        <ul>
          <li>
            <label htmlFor='firstName'>First Name *</label>
            <input type='text' id='firstName' />
          </li>
          <li>
            <label htmlFor='lastName'>Last Name *</label>
            <input type='text' id='lastName' />
          </li>
          <li>
            <label htmlFor='email'>E-mail Address *</label>
            <input type='text' id='email' />
          </li>
          <li>
            <label htmlFor='address'>Street Address</label>
            <input type='text' id='address' />
          </li>
          <li>
            <label htmlFor='city'>City</label>
            <input type='text' id='city' />
          </li>
          <li>
            <label htmlFor='state'>State</label>
            <StandardForm.Select options={states} />
          </li>
          <li>
            <label htmlFor='zip'>ZIP</label>
            <input type='text' id='zip' />
          </li>
          <li>
            <label htmlFor='phone'>Phone</label>
            <input type='text' id='phone' />
          </li>
          <li>
            <label htmlFor='subscribe'>Would you like to subscribe to our e-mail newsletter?</label>
            <StandardForm.Input>
              <label for='subscribe-yes'>
                <input type='radio' id='subscribe-yes' name='subscribe' />
                Yes, I would
              </label>
              <label for='subscribe-no'>
                <input type='radio' id='subscribe-no' name='subscribe' />
                No, I would not
              </label>
            </StandardForm.Input>
          </li>
          <li>
            <label htmlFor='regarding'>I am contacting you regarding a</label>
            <StandardForm.Input>
              <label htmlFor='regarding-cat'>
                <input type='radio' id='regarding-cat' name='regarding' />
                Cat
              </label>
              <label htmlFor='regarding-dog'>
                <input type='radio' id='regarding-dog' name='regarding' />
                Dog
              </label>
              <label htmlFor='regarding-other'>
                <input type='radio' id='regarding-other' name='regarding' />
                Other
              </label>
            </StandardForm.Input>
          </li>
          <li>
            <label htmlFor='comment'>Your comments or questions</label>
            <textarea id='comment' />
          </li>
        </ul>

        <button className={cx('btn')} type='submit'>Submit Contact Form</button>
      </StandardForm>
    </div>
  </StandardLayout>

export default Contact;
